import {
    Box, Chip, Stack, TableContainer, Table, TableHead, TableRow, TableCell, TableBody, Typography
} from '@mui/material';

const DepositTable = (props) => {
    return (
        <Box bgcolor="#212121" pt={4.5} pb={8} px={6} borderRadius={3}>
            <TableContainer>
                <Table sx={{ minWidth: 650 }} aria-label="simple table">
                    <TableHead>
                        <TableRow>
                            <TableCell></TableCell>
                            <TableCell align="right" sx={{ fontFamily: "SF Pro", fontSize: 13, fontWeight: 860, color: '#CEBFBF' }}>APY</TableCell>
                            <TableCell align="right" sx={{ fontFamily: "SF Pro", fontSize: 13, fontWeight: 860, color: '#CEBFBF' }}>Deposit Amount</TableCell>
                            <TableCell align="right" sx={{ fontFamily: "SF Pro", fontSize: 13, fontWeight: 860, color: '#CEBFBF' }}>Actions</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                    {props.rows.map((row) => (
                        <TableRow
                        key={row.type}
                        sx={{ borderTop: '1px solid #434040', '&:last-child td, &:last-child th': { border: 0 } }}
                        >
                            <TableCell component="th" scope="row">
                                <Stack flexDirection="row" alignItems="center" gap={2}>
                                    <img src={row.image} width={80} height={80} />
                                    <Box>
                                        <Typography sx={{ fontFamily: "SF Pro", fontSize: 20, fontWeight: 860, color: '#FFFFFF' }}>{row.type}</Typography>
                                        <Typography sx={{ fontFamily: "SF Pro", fontSize: 13, fontWeight: 860, color: '#CEBFBF' }}>Terra USD</Typography>
                                    </Box>
                                </Stack>
                                
                            </TableCell>
                            <TableCell align="right" sx={{ fontFamily: "SF Pro", fontSize: 13, fontWeight: 860, color: '#CEBFBF' }}>{row.percent}</TableCell>
                            <TableCell align="right" sx={{ fontFamily: "SF Pro", fontSize: 13, fontWeight: 860, color: '#CEBFBF' }}>{row.amount}</TableCell>
                            <TableCell align="right" sx={{ fontFamily: "SF Pro", fontSize: 13, fontWeight: 860, color: '#CEBFBF' }}>
                                <Chip clickable={true} label="Deposit" sx={{ bgcolor: '#493C3C', border: '1px solid #CEBFBF', color: '#FFFFFF', px: 3 }} />
                                <Chip clickable={true} label="Withraw" sx={{ bgcolor: 'transparent', border: '1px solid #CEBFBF', color: '#CEBFBF', ml: 2, px: 3 }} />
                            </TableCell>
                        </TableRow>
                    ))}
                    </TableBody>
                </Table>
            </TableContainer>
        </Box>
    );
}

export default DepositTable;