import { Box, Chip, Stack, Typography, Tooltip } from '@mui/material';
import SendIcon from '@mui/icons-material/Send';
import ErrorOutlineIcon from '@mui/icons-material/ErrorOutline';
import SwapHorizIcon from '@mui/icons-material/SwapHoriz';
import Chart from 'react-apexcharts';

const rows = [
    {
        title: 'UST wallet Ballence',
        price: '$ 65,633',
        color: '#000000',
    },
    {
        title: 'Deposit Total',
        price: '$ 532,875',
        color: '#57A146'
    },
    {
        title: 'UST Deposit',
        price: '$ 243,230',
        color: '#6493F1'
    },
    {
        title: 'LUNA Deposit',
        price: '$ 243,230',
        color: '#F9D85E'
    },
]

const data = {
    series: [60, 55],
    options: {
        labels: ["LUNA", "UST"],
        plotOptions: {
            pie: {
                expandOnClick: false,
                donut: {
                    labels: {
                        show: false,
                        name: {

                        },
                        value: {
                        }
                    }
                }
            }
        },
        chart: {
            width: 380,
            type: 'donut',
        },
        dataLabels: {
            enabled: false
        },
        responsive: [{
            breakpoint: 480,
            options: {
            chart: {
                width: 200
            },
            legend: {
                show: false
            }
            }
        }],
        legend: {
            position: 'right',
            offsetY: 0,
            height: 230,
        }
    },
};

const TotalValueBox = () => {
    return (
        <Box bgcolor="#212121" pt={7.5} pb={8} px={6} borderRadius={3} flex="1 0 40%">
            <Stack flexDirection="row" justifyContent="space-between">
                <Stack flexDirection="row" alignItems="center" gap={1}>
                    <Typography fontFamily="SF Pro" fontWeight={860} lineHeight={1.5} fontSize={20} color="#FFFFFF" textTransform="uppercase" >Total value</Typography>
                    <ErrorOutlineIcon sx={{ fontSize: 20, color: "#FFFFFF" }} />
                </Stack>
                <Chip 
                    icon={<SwapHorizIcon sx={{ color: '#CEBFBF !important' }} />}
                    label="Swap"
                    clickable
                    sx={{
                        color: '#CEBFBF',
                        px: 2,
                        bgcolor: '#212121',
                        border: '1px solid #CEBFBF',
                    }}
                />
            </Stack>
            <Stack flexDirection="row" alignItems="flex-end" pb={5}>
                <Typography component="div" fontWeight={860} fontFamily="SF Pro" fontSize={36} lineHeight="unset" color="#FFFFFF" >4,665,750</Typography>
                <Typography component="div" pl={.5} fontWeight={860} color="#FFFFFF">UST</Typography>
            </Stack>
            <Stack flexDirection="row" justifyContent="space-between">
                <Stack gap={1}>
                {rows.map( (row, key) => 
                    <Box key={key}>
                        <Stack flexDirection="row" alignItems="center" gap={1}>
                            <Box width={16} borderRadius={.25} height={16} bgcolor={row.color}></Box>
                            <Typography fontSize={20} fontWeight={860} color="#FFFFFF" fontFamily="SF Pro">{row.title}</Typography>
                        </Stack>
                        <Typography fontFamily="SF Pro" fontSize={14} fontStyle="italic" color="#CEBFBF">{row.price}</Typography>
                    </Box>
                )}
                </Stack>
                <Box>
                    <Chart options={data.options} series={data.series} type="donut" width={380} />
                </Box>
            </Stack>
        </Box>
    );
}

export default TotalValueBox;