import { Box, Container, Stack, Typography } from "@mui/material";
import OpenInNewIcon from '@mui/icons-material/OpenInNew';
import TotalValueBox from "./TotalValueBox";
import TotalPayedBox from "./TotalPayedBox";
import TransactionHistory from "./TransactionHistory";
import DepositTable from "./DepositTable";
import USTFlag from '../../assets/images/UST.png';
import LUNAFlag from '../../assets/images/LUNA.png';

const rows = [
    {
        rows: [
            {
                image: USTFlag,
                type: 'UST',
                percent: '34.87%',
                amount: '25.016 UST',
            }
        ]
    },
    {
        rows: [
            {
                image: LUNAFlag,
                type: 'UST',
                percent: '34.87%',
                amount: '25.016 UST',
            }
        ]
    },
]

const Mypage = () => {
    return (
        <Box pt={20}>
            <Container maxWidth="xl">
                <Stack flexDirection="row" justifyContent="space-between" alignItems="flex-end" pb={7.5}>
                    <Stack flexDirection="row" alignItems="flex-end" gap={2}>
                        <Typography variant="div" 
                            color="#FFFFFF"
                            fontFamily={'SF Pro'}
                            fontSize={40}
                            fontWeight={860}
                            lineHeight={.8}
                            textTransform="uppercase"
                        >My page</Typography>
                        <Stack flexDirection="row" alignItems="flex-end">
                            <Typography component="div" 
                                fontSize={13} 
                                fontFamily='SF Pro'
                                fontWeight={860} 
                                lineHeight={1} 
                                color="#CEC0C0">Docs</Typography>
                            <OpenInNewIcon sx={{ color: '#CEC0C0' }} fontSize="13" />
                        </Stack>
                    </Stack>
                    {/* <Stack flexDirection="row">
                        <Typography fontWeight={700}>1 LUNA &asymp; </Typography>
                        <Typography fontWeight={700}>&nbsp;1.212 UST</Typography>
                    </Stack> */}
                </Stack>
                <Stack gap={5}>
                    <Stack flexDirection="row" gap={5}>
                        <TotalValueBox />
                        <TotalPayedBox />
                    </Stack>
                    {rows.map( (row, key) => 
                        <DepositTable key={key} {...row} />
                    )}
                    <TransactionHistory />
                </Stack>
            </Container>
        </Box>
    );
}

export default Mypage;