import { Box, Chip, Stack, Typography } from '@mui/material'; 
import ErrorOutlineIcon from '@mui/icons-material/ErrorOutline';

const TotalPayedBox = () => {
    return (
        <Box bgcolor="#212121" pt={7.5} pb={8} px={6} borderRadius={3} flex="1 0 40%">
            <Typography fontFamily="SF Pro" fontWeight={860} lineHeight={1.5} fontSize={20} color="#FFFFFF" textTransform="uppercase" >Total payed interest</Typography>
            <Stack flexDirection="row" alignItems="flex-end">
                <Typography component="div" fontWeight={860} fontFamily="SF Pro" fontSize={36} lineHeight="unset" color="#FFFFFF" >93,332.75</Typography>
                <Typography component="div" pl={.5} fontWeight={860} color="#FFFFFF">UST</Typography>
            </Stack>
            <Typography fontSize={14} fontStyle="italic" color="#CEBFBF" fontFamily="SF Pro">USD $1.75</Typography>
            <Stack flexDirection="row" gap={1} alignItems="center" pt={5}>
                <Typography fontSize={20} fontFamily="SF Pro" fontWeight={860} color="#FFFFFF" textTransform="uppercase">Total days staked</Typography>
                <ErrorOutlineIcon  sx={{ fontSize: 20, color: '#FFFFFF' }} />
            </Stack>
            <Stack flexDirection="row" alignItems="flex-end" pt={2}>
                <Typography color="#FFFFFF" fontSize={36} fontWeight={860} fontFamily="SF Pro" lineHeight={.8}>17</Typography>
                <Typography color="#FFFFFF" fontSize={20} fontWeight={860} fontFamily="SF Pro" lineHeight={1}>days</Typography>
            </Stack>
            <Box pt={14}>
            <Chip label="Stake More"
                    clickable={true}
                    sx={{ 
                        width: '100%',
                        bgcolor: '#493C3C', 
                        color: '#FFFFFF', 
                        fontSize: 13,
                        px: 12,
                        py: 3,
                        borderRadius: 40,
                        textTransform: 'uppercase'
                    }} />
            </Box>
        </Box>
    );
}

export default TotalPayedBox;